
html[data-v-3], body[data-v-3], #editor[data-v-3] {
  margin: 0;
  height: 100%;
  font-family: 'Helvetica Neue', Arial, sans-serif;
  color: #333;
}
textarea[data-v-3], #editor div[data-v-3] {
  /*display: inline-block;*/
  width: 70%;
  height: 100%;
  vertical-align: top;
  box-sizing: border-box;
  padding: 0 20px;
}
textarea[data-v-3] {
  border: none;
  border-right: 1px solid #ccc;
  resize: none;
  outline: none;
  background-color: #f6f6f6;
  font-size: 14px;
  font-family: 'Monaco', courier, monospace;
  padding: 20px;
}
code[data-v-3] {
  color: #f66;
}



